:host {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  overflow: hidden;
  min-width: 0; // Allow flex shrinking

  // Ensure the root container doesn't cause horizontal overflow
  > div {
    min-width: 0;
    max-width: 100%;
  }
}

.flex-1 {
  flex: 1 1 auto;
  min-height: 0; /* Important for Firefox */
}

::ng-deep {
  .p-component-outlet {
    height: 100%;
  }

  .p-component-outlet > * {
    height: 100%;
    display: block;
  }

  .state-popover {
    background: var(--input-background-color);
    animation: fadeIn 0.2s ease-in-out;
  }

  .state-overlay-panel {
    border-radius: 0.5rem;
    min-width: 220px;
    max-width: min(320px, 90vw);
    padding: 0;

    .p-popover-content {
      padding: 0;
    }
  }

  // Responsive toolbar button layout
  .p-toolbar {
    min-width: 0; // Allow toolbar to shrink
    flex-shrink: 1;

    .p-toolbar-group-start {
      flex-shrink: 1;
      min-width: 0;
      overflow: hidden;

      h2 {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    .p-toolbar-group-end {
      flex-shrink: 0; // Don't shrink the action buttons
      min-width: 0;

      // Important: Override PrimeNG's default display
      display: flex !important;

      // The actual container div inside should be scrollable
      .toolbar-actions-container {
        overflow-x: auto;
        overflow-y: hidden;
        display: flex;
        gap: 0.5rem;
        align-items: center;
        max-width: 100%;
        flex-wrap: nowrap;
        position: relative;

        // Enable smooth scrolling
        scroll-behavior: smooth;
        -webkit-overflow-scrolling: touch;

        // Hide scrollbar for cleaner look but keep functionality
        scrollbar-width: thin;
        scrollbar-color: rgba(0, 0, 0, 0.2) transparent;

        &::-webkit-scrollbar {
          height: 4px;
        }

        &::-webkit-scrollbar-track {
          background: transparent;
        }

        &::-webkit-scrollbar-thumb {
          background-color: rgba(0, 0, 0, 0.2);
          border-radius: 2px;
        }

        &::-webkit-scrollbar-thumb:hover {
          background-color: rgba(0, 0, 0, 0.3);
        }

        // Subtle gradient to indicate more content on scroll
        &::after {
          content: '';
          position: absolute;
          right: 0;
          top: 0;
          bottom: 4px;
          width: 30px;
          background: linear-gradient(
            to right,
            transparent,
            var(--surface-0, #ffffff) 70%
          );
          pointer-events: none;
          opacity: 0;
          transition: opacity 0.2s ease;
        }

        // Show gradient when scrollable
        &:not(:hover)::after {
          opacity: 1;
        }

        // Ensure all children don't shrink
        > * {
          flex-shrink: 0;
        }

        // Ensure icons and buttons maintain size
        button,
        span,
        svg,
        div {
          flex-shrink: 0;
        }
      }
    }
  }
}

// Additional styles for toolbar actions container at root level
.toolbar-actions-container {
  flex-wrap: nowrap !important; // Prevent wrapping to enable horizontal scroll
  -webkit-overflow-scrolling: touch; // Smooth scrolling on iOS
  max-width: 100%;
  min-width: 0;

  // Ensure all children don't shrink or wrap
  > * {
    flex-shrink: 0 !important;
  }
}

// Ensure details panel doesn't overflow
:host ::ng-deep {
  app-details-panel {
    min-width: 0;
    max-width: 100%;
  }
}

// Additional responsive adjustments for narrow panels
@media (max-width: 1024px) {
  ::ng-deep {
    .p-toolbar {
      .p-toolbar-group-start {
        h2 {
          font-size: 0.95rem;
        }
      }

      .p-toolbar-group-end {
        .toolbar-actions-container {
          gap: 0.375rem; // Slightly tighter spacing on smaller screens
        }
      }
    }
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hover:hover {
  opacity: 1 !important;
  color: var(--color-primary-color);
}
